'use client';

import { useState } from 'react';

export default function Home() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <div className="min-h-screen bg-[var(--background)] text-[var(--text-main)] flex flex-col">
      {/* Navigation */}
      <nav className="fixed top-0 w-full bg-[var(--surface)]/80 backdrop-blur-md border-b border-[var(--primary)]/10 z-50">
        <div className="max-w-3xl mx-auto px-6">
          <div className="flex justify-between items-center h-16">
            {/* Logo */}
            <div className="text-xl font-bold text-[var(--primary)]">
              TOURNIFY
            </div>

            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center space-x-8">
              <a href="#tournaments" className="text-[var(--text-muted)] hover:text-[var(--secondary)] transition-colors duration-300">
                Tournaments
              </a>
              <a href="#community" className="text-[var(--text-muted)] hover:text-[var(--secondary)] transition-colors duration-300">
                Community
              </a>
            </div>

            {/* Auth Buttons */}
            <div className="hidden md:flex items-center space-x-4">
              <button className="px-4 py-2 text-[var(--text-main)] hover:text-[var(--secondary)] transition-colors duration-300">
                Sign In
              </button>
              <button className="px-6 py-2 bg-[var(--primary)] text-white rounded-lg hover:bg-[var(--primary)]/90 transition-all duration-300">
                Sign Up
              </button>
            </div>

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="text-[var(--text-main)] hover:text-[var(--secondary)] transition-colors duration-300"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                </svg>
              </button>
            </div>
          </div>

          {/* Mobile Navigation */}
          {isMenuOpen && (
            <div className="md:hidden bg-[var(--surface)] border-t border-[var(--primary)]/10">
              <div className="px-4 py-4 space-y-3">
                <a href="#tournaments" className="block text-[var(--text-muted)] hover:text-[var(--secondary)] transition-colors duration-300">
                  Tournaments
                </a>
                <a href="#community" className="block text-[var(--text-muted)] hover:text-[var(--secondary)] transition-colors duration-300">
                  Community
                </a>
                <div className="pt-3 border-t border-[var(--primary)]/10 space-y-2">
                  <button className="block w-full text-left text-[var(--text-main)] hover:text-[var(--secondary)] transition-colors duration-300">
                    Sign In
                  </button>
                  <button className="block w-full text-left text-[var(--text-main)] hover:text-[var(--secondary)] transition-colors duration-300">
                    Sign Up
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      </nav>

      {/* Main Content - Centered */}
      <main className="flex-1 flex flex-col justify-center">
        {/* Hero Section */}
        <section className="px-6 py-20">
          <div className="max-w-3xl mx-auto text-center">
            {/* Main Heading */}
            <h1 className="text-5xl md:text-7xl font-bold mb-8 leading-tight">
              <span className="text-[var(--primary)]">ðŸŽ®</span>
              <br />
              <span className="text-[var(--text-main)]">JOIN. PLAY. WIN.</span>
            </h1>
            
            {/* Subheading */}
            <p className="text-xl text-[var(--text-muted)] mb-12 max-w-2xl mx-auto">
              Valorant-style tournaments for everyone.
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row gap-4 justify-center mb-8">
              <button className="px-8 py-4 bg-[var(--primary)] text-white rounded-lg text-lg font-semibold hover:bg-[var(--primary)]/90 transition-all duration-300 transform hover:scale-105">
                Join a Tournament
              </button>
              <button className="px-8 py-4 border-2 border-[var(--secondary)] text-[var(--secondary)] rounded-lg text-lg font-semibold hover:bg-[var(--secondary)] hover:text-[var(--background)] transition-all duration-300 transform hover:scale-105">
                Host a Match
              </button>
            </div>

            {/* Secondary Action */}
            <div className="flex justify-center">
              <button className="flex items-center gap-2 text-[var(--text-muted)] hover:text-[var(--secondary)] transition-colors duration-300">
                <span className="text-xl">ðŸŽ§</span>
                Join our Discord
              </button>
            </div>
          </div>
        </section>

        {/* Simple Stats */}
        <section className="px-6 py-16 bg-[var(--surface)]/20">
          <div className="max-w-3xl mx-auto">
            <div className="grid grid-cols-3 gap-8 text-center">
              <div>
                <div className="text-3xl font-bold text-[var(--primary)] mb-2">10K+</div>
                <div className="text-[var(--text-muted)]">Players</div>
              </div>
              <div>
                <div className="text-3xl font-bold text-[var(--secondary)] mb-2">500+</div>
                <div className="text-[var(--text-muted)]">Tournaments</div>
              </div>
              <div>
                <div className="text-3xl font-bold text-[var(--success)] mb-2">$50K</div>
                <div className="text-[var(--text-muted)]">Prize Pool</div>
              </div>
            </div>
          </div>
        </section>
      </main>

      {/* Footer */}
      <footer className="py-12 px-6 bg-[var(--surface)]/30 border-t border-[var(--primary)]/10">
        <div className="max-w-3xl mx-auto">
          <div className="flex flex-col md:flex-row justify-between items-center">
            <div className="text-[var(--primary)] font-bold text-xl mb-4 md:mb-0">
              TOURNIFY
            </div>
            <div className="flex items-center space-x-6 text-[var(--text-muted)]">
              <a href="#" className="hover:text-[var(--secondary)] transition-colors duration-300">Privacy</a>
              <a href="#" className="hover:text-[var(--secondary)] transition-colors duration-300">Terms</a>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}
